.about {
  box-sizing: border-box;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

.about__wrapper {
  box-sizing: border-box;
  width: fit-content;
  margin: 0 auto;
  padding: 30px 16px 60px;
}

.about__content {
  vertical-align: middle;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.about__title {
  height: 66px;
}

.about__subtitle {
  height: 68px;
}

.about__text {
  margin-bottom: 16px;
}

.about__pictures {
  box-sizing: border-box;
  flex-flow: column wrap;
  justify-content: center;
  row-gap: 16px;
  width: fit-content;
  margin: 0 auto;
  display: flex;
}

.about__picture {
  width: 288px;
  height: 258px;
}

.about__picture-wrap {
  z-index: 1;
  position: relative;
}

.about__picture-overlay {
  z-index: 40;
  color: #fff;
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  height: 83px;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  position: absolute;
  inset: 175px 0 0 14px;
}

.about__picture-text {
  width: 210px;
}

.about__picture-wrap:after, .about__picture-wrap.active:after {
  content: "";
  z-index: 25;
  pointer-events: none;
  background: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%);
  width: 288px;
  height: 83px;
  position: absolute;
  inset: 175px 0 0;
}

@media (width >= 768px) {
  .about__wrapper {
    padding: 28px 16px 58px 10px;
  }

  .about__picture {
    width: 320px;
    height: 282px;
  }

  .about__pictures {
    flex-direction: row;
    column-gap: 18px;
  }

  .about__text {
    width: fit-content;
    margin-bottom: 48px;
  }

  .about__picture-overlay {
    opacity: 0;
    box-sizing: border-box;
    flex-direction: column;
    width: 316px;
    height: 283px;
    padding: 109px 0 93px;
    transition: opacity .5s linear;
    top: -7px;
    bottom: 0;
    left: -6px;
  }

  .about__picture-wrap:hover .about__picture-overlay, .about__picture-wrap.active .about__picture-overlay {
    opacity: 1;
    transition: opacity .3s;
  }

  .about__picture-wrap:after, .about__picture-wrap.active:after {
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: opacity .3s linear;
    display: flex;
    top: -7px;
    bottom: 0;
    left: -6px;
  }

  .about__picture-wrap:hover:after, .about__picture-wrap.active:after {
    opacity: 1;
    transition: opacity .3s linear;
  }
}

@media (width >= 1400px) {
  .about__wrapper {
    padding: 42px 0 154px;
  }

  .about__picture {
    width: 370px;
    height: 338px;
  }

  .about__pictures {
    flex-wrap: nowrap;
    gap: 30px;
  }

  .about__text {
    margin-bottom: 58px;
  }

  .about__picture-overlay {
    width: 370px;
    height: 338px;
    padding: 130px 0 110px;
    top: -16px;
    left: -16px;
  }

  .about__picture-wrap:after, .about__picture-wrap.active:after {
    opacity: 0;
    width: 100%;
    height: 100%;
    display: flex;
    top: -16px;
    bottom: 0;
    left: -16px;
  }
}
